<template>
  <div class="plan-card" :class="{ 'plan-card--premium': isPremium }">
    <div class="plan-card__glow" v-if="isPremium"></div>

    <div class="plan-card__header">
      <div class="plan-card__icon-wrapper" :class="{ 'plan-card__icon-wrapper--premium': isPremium }">
        <v-icon :icon="isPremium ? 'mdi-crown' : 'mdi-leaf'" size="28" />
      </div>
      <div class="plan-card__title-group">
        <span class="plan-card__label">Текущий план</span>
        <span class="plan-card__name">{{ subscription.planName }}</span>
      </div>
      <v-chip
        v-if="isPremium"
        size="small"
        class="plan-card__badge"
      >
        PRO
      </v-chip>
    </div>

    <div class="plan-card__content" v-if="isPremium">
      <div class="plan-card__info-row">
        <v-icon icon="mdi-calendar-check" size="18" class="plan-card__info-icon" />
        <span class="plan-card__info-label">Активна до</span>
        <span class="plan-card__info-value">{{ formattedEndDate }}</span>
      </div>

      <div class="plan-card__info-row">
        <v-icon icon="mdi-clock-outline" size="18" class="plan-card__info-icon" />
        <span class="plan-card__info-label">Осталось</span>
        <span class="plan-card__info-value plan-card__info-value--highlight">
          {{ subscription.daysRemaining }} {{ daysWord }}
        </span>
      </div>

      <div class="plan-card__progress">
        <div
          class="plan-card__progress-bar"
          :style="{ width: progressPercent + '%' }"
        ></div>
      </div>
    </div>

    <div class="plan-card__content plan-card__content--free" v-else>
      <p class="plan-card__free-text">
        Базовые возможности для ваших заметок
      </p>
      <div class="plan-card__features">
        <span class="plan-card__feature">
          <v-icon icon="mdi-check" size="16" /> Создание заметок
        </span>
        <span class="plan-card__feature">
          <v-icon icon="mdi-check" size="16" /> Базовые блоки
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CurrentPlanCard',
  props: {
    subscription: {
      type: Object,
      required: true,
    },
  },
  computed: {
    isPremium() {
      return this.subscription.plan === 'PREMIUM'
    },
    formattedEndDate() {
      if (!this.subscription.endDate) return ''
      const date = new Date(this.subscription.endDate)
      return date.toLocaleDateString('ru-RU', {
        day: 'numeric',
        month: 'long',
        year: 'numeric'
      })
    },
    daysWord() {
      const days = this.subscription.daysRemaining
      if (days % 10 === 1 && days % 100 !== 11) return 'день'
      if ([2, 3, 4].includes(days % 10) && ![12, 13, 14].includes(days % 100)) return 'дня'
      return 'дней'
    },
    progressPercent() {
      // Assuming 30 days subscription cycle
      const total = 30
      const remaining = this.subscription.daysRemaining || 0
      return Math.min(100, Math.max(0, (remaining / total) * 100))
    },
  },
}
</script>

