<template>
  <div class="subscription-page">
    <!-- Header -->
    <div class="subscription-header">
      <button class="subscription-header__back" @click="goBack">
        <v-icon icon="mdi-arrow-left" size="24" />
      </button>
      <div class="subscription-header__bg"></div>
      <div class="subscription-header__content">
        <div class="subscription-header__icon">
          <v-icon icon="mdi-star-four-points" size="28" />
        </div>
        <h1 class="subscription-header__title">Подписка</h1>
        <p class="subscription-header__subtitle">Управляйте вашим планом</p>
      </div>
    </div>

    <!-- Content -->
    <div class="subscription-content">
      <SubscriptionSkeleton v-if="loading" />

      <template v-else>
        <transition name="fade-up" appear>
          <CurrentPlanCard
            :subscription="subscription"
          />
        </transition>

        <transition name="fade-up" appear>
          <LimitsCard
            :limits="limits"
            style="transition-delay: 0.1s"
          />
        </transition>

        <transition name="fade-up" appear>
          <SubscriptionCTA
            :text="ctaText"
            :is-upgrade="limits?.canUpgrade"
            style="transition-delay: 0.2s"
            @click="goToPlans"
          />
        </transition>
      </template>
    </div>
  </div>
</template>

<script>
import CurrentPlanCard from '@/components/subscription/CurrentPlanCard.vue'
import LimitsCard from '@/components/subscription/LimitsCard.vue'
import SubscriptionCTA from '@/components/subscription/SubscriptionCTA.vue'
import SubscriptionSkeleton from '@/components/subscription/SubscriptionSkeleton.vue'

import { SubscriptionCall } from '@/api/call'

export default {
  name: 'SubscriptionView',
  components: {
    CurrentPlanCard,
    LimitsCard,
    SubscriptionCTA,
    SubscriptionSkeleton,
  },
  data() {
    return {
      loading: true,
      subscription: null,
      limits: null,
    }
  },
  computed: {
    ctaText() {
      return this.limits?.canUpgrade
        ? 'Улучшить подписку'
        : 'Управление подпиской'
    },
  },
  async mounted() {
    const [sub, lim] = await Promise.all([
      SubscriptionCall.getCurrent(),
      SubscriptionCall.getLimits(),
    ])

    this.subscription = sub.data
    this.limits = lim.data
    this.loading = false
  },
  methods: {
    goBack() {
      this.$router.push({ name: 'pages' })
    },
    goToPlans() {
      this.$router.push('/subscription/plans')
    },
  },
}
</script>

