<template>
  <div class="limits-card">
    <div class="limits-card__header">
      <v-icon icon="mdi-chart-box-outline" size="22" class="limits-card__header-icon" />
      <span class="limits-card__title">Использование</span>
    </div>

    <div class="limits-card__items">
      <!-- Pages limit -->
      <div class="limit-item">
        <div class="limit-item__header">
          <div class="limit-item__icon-wrapper">
            <v-icon icon="mdi-file-document-outline" size="18" />
          </div>
          <span class="limit-item__label">Страницы</span>
          <span class="limit-item__value">
            <span class="limit-item__current">{{ limits.currentPages }}</span>
            <span class="limit-item__separator">/</span>
            <span class="limit-item__max">{{ limits.maxPages }}</span>
          </span>
        </div>
        <div class="limit-item__progress">
          <div
            class="limit-item__progress-bar"
            :class="{ 'limit-item__progress-bar--warning': pagesPercent > 80 }"
            :style="{ width: pagesPercent + '%' }"
          ></div>
        </div>
      </div>

      <!-- Blocks per page -->
      <div class="limit-item">
        <div class="limit-item__header">
          <div class="limit-item__icon-wrapper">
            <v-icon icon="mdi-view-grid-outline" size="18" />
          </div>
          <span class="limit-item__label">Блоков на страницу</span>
          <span class="limit-item__value">
            <span class="limit-item__max">{{ limits.maxBlocksPerPage }}</span>
          </span>
        </div>
        <div class="limit-item__info">
          <v-icon icon="mdi-infinity" size="14" v-if="limits.maxBlocksPerPage >= 1000" />
          <span v-else>макс.</span>
        </div>
      </div>

      <!-- Export feature -->
      <div class="limit-item limit-item--feature">
        <div class="limit-item__header">
          <div class="limit-item__icon-wrapper" :class="{ 'limit-item__icon-wrapper--active': limits.canExport }">
            <v-icon icon="mdi-export" size="18" />
          </div>
          <span class="limit-item__label">Экспорт заметок</span>
          <div class="limit-item__status" :class="{ 'limit-item__status--active': limits.canExport }">
            <v-icon :icon="limits.canExport ? 'mdi-check-circle' : 'mdi-lock'" size="16" />
            <span>{{ limits.canExport ? 'Доступно' : 'PRO' }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'LimitsCard',
  props: {
    limits: {
      type: Object,
      required: true,
    },
  },
  computed: {
    pagesPercent() {
      if (!this.limits.maxPages) return 0
      return Math.min(100, (this.limits.currentPages / this.limits.maxPages) * 100)
    },
  },
}
</script>

